import Vue from 'vue';
import VueRouter from 'vue-router';
import home from '../pages/home.vue';
import store from '../store';
import Cookie from '../utils/cookie.js';
import { Message } from 'view-design';
Vue.use(VueRouter);

const routes = [
  {
    path: '/login',
    name: 'login',
    component: home
  },
  {
    path: '/about',
    name: 'about',
    meta: {
      publicAccess: false,
    },
    component: () => import('../views/About.vue')
  },
  {
    path: '/main',
    name: 'Artical & Repos',
    meta: {
      publicAccess: true,
      keepAlive:true
    },
    component: () => import('../pages/main/main.vue')
  },
  {
    path: '/aboutMe',
    name: 'About Me',
    meta: {
      publicAccess: false,
      keepAlive:true
    },
    component: () => import('../pages/aboutMe/aboutMe.vue')
  }
];
let history = [];
const addPath = (name,path) => history.push({name,path});
const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
});

router.beforeEach((to, from, next) => {
  addPath(to.name,to.path);
  store.commit('SET_HISTORY', history);
  if (to.path == '/login' || to.meta.publicAccess) {
  
    next();
  } else {
    if (store.state.token || Cookie.getAttribute('token')) {
   
      next();
    } else {
      Message.error('请先登录!');
      next('/login');
    }
  }
});

export default router;
