<template>
	<div id="themeButton">
		<div id="button" @click="handleClick($_bgCls)" :class="$_bgCls"></div>
	</div>
</template>

<script>
	import { black } from "color-name";
	export default {
		name: "themeButton",
		props: {
			bgColor: {
				type: String,
				default: "black"
			}
		},
		data() {
			return {};
		},
		computed: {
			$_bgCls() {
				return this.bgColor === "white" ? "black" : "white";
			}
		},
		methods: {
			handleClick(bgCls) {
				this.$emit("onClick", bgCls);
			}
		}
	};
</script>

<style lang="scss" scope>
#button {
	position: fixed;
	right: 20px;
	bottom: 50px;
	width: 30px;
	height: 30px;
	border-radius: 50%;
	transition: all 0.6s;
	&:hover {
		cursor: pointer;
		transform: scale(1.15);
	}
}
.black {
	background: black;
}
.white {
	background: white;
}
</style>